.new-task {
    background-color: #2a2a72;
    color: white;
    padding: 1rem;
    margin: 2rem auto;
    width: 50rem;
    max-width: 95%;
    border-radius: 8px;
    text-align: center;
    box-shadow: 0 1px 8px rgba(0, 0, 0, 0.25);
  }
  
  .new-task button {
    font: inherit;
    cursor: pointer;
    padding: .75rem 2rem;
    border: 1px solid black;
    background-color: white;
    color: black;
    border-radius: 5px;
    margin-right: 1rem;
  }
  
  .new-task button:hover,
  .new-task button:active {
    background-color: #0582CA;
    border-color: #0582CA;
    color: white;
  }
  
  .new-task button.alternative {
    color: #220131;
    border-color: transparent;
    background-color: transparent;
  }
  
  .new-task button.alternative:hover,
  .new-task button.alternative:active {
    background-color: #2a2a72;
  }